.sticky {
    -webkit-box-shadow: #DDD 0px 1px 2px;
    position: relative;
    background-color: #F4F39E;
    border-color: #DEE184;
    color: #47576B;
    text-align: center;
    margin: 2.5em 0px;
    padding: 1.5em 1em;
    box-shadow: 0px 1px 3px rgba(0,0,0,0.25);
    width: 250px;
    font-family: 'Indie Flower', cursive;
    font-family: 'Give You Glory', cursive;
    font-family: 'The Girl Next Door', cursive;
    font-family: 'Gloria Hallelujah', cursive;
  }

  .taped {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
  .sticky.taped:after {
    display: block;
    content: "";
    position: absolute; 
    width: 110px;
    height: 30px;
    top: -21px;
    left: 30%;    
    border: 1px solid #fff;
    background: rgba(254, 254, 254, .6);
    box-shadow: 0px 0 3px rgba(0,0,0,0.1);  
  }
  
  .note { 
  -webkit-box-shadow: #DDD 0px 1px 2px;
    position: relative;
    background-color: #F4F39E;
    border-color: #DEE184;
    text-align: left;
    margin: 1.5em auto;
    padding: 1.5em 2em;
    box-shadow: 0px 1px 3px rgba(0,0,0,0.25);
    -webkit-transform: rotate(2deg);
    transform: rotate(2deg);
 display: flex;
 flex-direction: column;
 align-items: flex-start;
 justify-content: space-between;
    font-family: 'The Girl Next Door', cursive; /*originally with brain flower font*/
  
  
  height: 35em;
  cursor: pointer;

  }
  .note:after {
    display: block;
    content: "";
    position: absolute; 
    width: 110px;
    height: 30px;
    top: -21px;
    left: 30%;    
    border: 1px solid #fff;
    background: rgba(254, 254, 254, .6);
    box-shadow: 0px 0 3px rgba(0,0,0,0.1);  
  }

  .note::before {
    display: block;
    content: "";
    position: absolute; 
    width: 200px;
    height: 30px;
    top: 15px;
    left: 75%;    
    border: 1px double  rgba(236, 236, 234, 0.6);
    background: rgba(255, 255, 255, 0.6);
    box-shadow: 0px 0 3px rgba(0,0,0,0.1);  
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    
  }
  
  

  .email{
    width: 100%;
    background: #F4F39E;
    border: 1px solid  #47576B;
   color: #47576B;
   border-radius: 4px;
  }

