:root {
  --step--2: clamp(0.91rem, calc(0.89rem + 0.1vw), 0.96rem);
  --step--1: clamp(1.09rem, calc(1.05rem + 0.21vw), 1.2rem);
  --step-0: clamp(1.31rem, calc(1.24rem + 0.37vw), 1.5rem);
  --step-1: clamp(1.58rem, calc(1.46rem + 0.59vw), 1.88rem);
  --step-2: clamp(1.89rem, calc(1.71rem + 0.89vw), 2.34rem);
  --step-3: clamp(2.27rem, calc(2.01rem + 1.29vw), 2.93rem);
  --step-4: clamp(2.72rem, calc(2.36rem + 1.83vw), 3.66rem);
  --step-5: clamp(3.27rem, calc(2.75rem + 2.56vw), 4.58rem);

  --color-3: #454545;
  --color-2: #7a7d7d;
  --color-1: #e5ecf4;

  letter-spacing: 0.75px;
}

body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat Alternates", sans-serif;
  font-family: "Mulish", sans-serif;

  background: #e5ecf4;
}

ul{
list-style: none;

}

.section {
  width: min(1200px, 100%);
  margin-inline: auto;


}
textarea:focus,
input:focus {
 outline: 1px solid     #47576B;;

}

label {
  color: #454545;
  color: var(--color-3);
  font-size: clamp(1.09rem, calc(1.05rem + 0.21vw), 1.2rem);
  font-size: var(--step--1);
}
textarea{
  width: 100%;
  height: 100%;
  resize: none;
}

textarea,
input {
  cursor: pointer;
 letter-spacing: 1px;
  color: #454545;
  color: var(--color-3);
  outline: none;
  font-size: clamp(1.09rem, calc(1.05rem + 0.21vw), 1.2rem);
  font-size: var(--step--1);
  padding: .5em;
 
}

a {
  text-decoration: none;
  color: #454545;
  color: var(--color-3);
  cursor: pointer;
}
p {
  font-size: clamp(1.31rem, calc(1.24rem + 0.37vw), 1.5rem);
  font-size: var(--step-0);
  color: #454545;
  color: var(--color-3);

}
.title {
  font-size: clamp(2.75rem, 2vw, 2.5rem);
}
.btn {
  font-size: clamp(0.95rem, 1.5vw, 1.5rem);
  padding: 5px 20px;
  margin-bottom: 0.5rem;
cursor: pointer;
  border: 1px dashed #454545;
  border: 1px dashed var(--color-3);
  background-color: #dcfca9;
  color: #454545;
  color: var(--color-3);
  border-radius: 2px;

  font-family: "Mulish", sans-serif;
}

.btn:hover {
  color: #292929;
  background-color: #bfeb7a;
  border-radius: 0;
}

@media screen and (max-width: 768px) {
  .section {
    width: 100%;
    margin-inline: auto;
  }

  .btn {
    font-size: 15px;
  }
}

